	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/git-zero-to-git-hero/index.md absPath of file >>> MarkdownRemark","html":"<h1>Git Zero to Git Hero</h1>\n<h2>Mastering git requires a mentor. Let me be yours.</h2>\n<hr>\n<h3>Getting started with git</h3>\n<p>If reading is your thing, be sure to check out these ‘git basics’ guides. All of them cover the same content, but with a slightly different angle.</p>\n<p><a href=\"/media/58e65ed920f507c169efa0796735d1fe/href\" title=\"/media/58e65ed920f507c169efa0796735d1fe/href\"><strong>Hello World</strong><br>\n<em>The Hello World project is a time-honored tradition in computer programming. It is a simple exercise that gets you…</em>guides.github.com</a><a href=\"/media/58e65ed920f507c169efa0796735d1fe/href\"></a></p>\n<p><a href=\"/media/53f2738abfe700c0382b55f3f2268b2d/href\" title=\"/media/53f2738abfe700c0382b55f3f2268b2d/href\"><strong>Get Started with Git</strong><br>\n<em>Version control: It isn't just for coders anymore. If you're a writer, editor, or a designer who works iteratively on…</em>alistapart.com</a><a href=\"/media/53f2738abfe700c0382b55f3f2268b2d/href\"></a></p>\n<p><a href=\"/media/d2d4b521c465fac945bc66f0f6cf51e4/href\" title=\"/media/d2d4b521c465fac945bc66f0f6cf51e4/href\"><strong>git - the simple guide</strong><br>\n<em>just a simple guide for getting started with git. no deep shit ;)</em>rogerdudler.github.io</a><a href=\"/media/d2d4b521c465fac945bc66f0f6cf51e4/href\"></a></p>\n<p><a href=\"/media/b39b5cff0d1c976708c8cbe59e3de05b/href\" title=\"/media/b39b5cff0d1c976708c8cbe59e3de05b/href\"><strong>Git Basics Course</strong><br>\n<em>If you're serious about pursuing a career as a software developer or designer, at some point, you will need to learn a…</em>teamtreehouse.com</a><a href=\"/media/b39b5cff0d1c976708c8cbe59e3de05b/href\"></a></p>\n<p>Okay, so you’ve got the basics. The next question is, how do you improve your git technique and become a git master?</p>\n<h3>Write better commit messages</h3>\n<p>Your commit messages exist for <em>you</em> and <em>your benefit</em>, so you’re doing yourself a disservice to not be writing them in a way that will actually be useful to you in a year, six months, a week, a day or even an hour.</p>\n<p>Go out and write yourself the best darn messages you can, all the time, with the help of this post from Tim Pope:</p>\n<p><a href=\"/media/7b777ea6d7eaf587890e2fb9046e5d67/href\" title=\"/media/7b777ea6d7eaf587890e2fb9046e5d67/href\"><strong>A Note About Git Commit Messages</strong><br>\n<em>I want to take a moment to elaborate on what makes a well formed commit message. I think the best practices for commit…</em>tbaggery.com</a><a href=\"/media/7b777ea6d7eaf587890e2fb9046e5d67/href\"></a></p>\n<h3>Branches and branching strategy</h3>\n<p>A solid understanding of branching will make sure that your commit logs actually make sense historically, allows you to work with parallel versions of your source code and keep your master branch focussed, neat and tidy.</p>\n<p>Our recommended branching ‘strategy’ is:</p>\n<ul>\n<li>The <strong>master</strong> branch represents your <em>stable</em> release.</li>\n<li>The <strong>development</strong> branch is for your <em>work in progress.</em></li>\n<li>Individual <strong>features</strong> should be developed in their own branch.</li>\n<li><strong>Features</strong> are branched from the <strong>development</strong> branch.</li>\n<li><em>Completed</em> <strong>features</strong> are merged into the <strong>development</strong> branch.</li>\n<li><em>Stable</em> <strong>development</strong> branches get merged into the <strong>master</strong> branch.</li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/1*Scw6Tuap3aDVVldTePkFIw-d815a010304e0855a68ae40fdc0674ce-fa57c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 104.47916666666669%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 Scw6Tuap3aDVVldTePkFIw\"\n        title=\"\"\n        src=\"/static/1*Scw6Tuap3aDVVldTePkFIw-d815a010304e0855a68ae40fdc0674ce-082ce.png\"\n        srcset=\"/static/1*Scw6Tuap3aDVVldTePkFIw-d815a010304e0855a68ae40fdc0674ce-c7cb1.png 158w,\n/static/1*Scw6Tuap3aDVVldTePkFIw-d815a010304e0855a68ae40fdc0674ce-93041.png 315w,\n/static/1*Scw6Tuap3aDVVldTePkFIw-d815a010304e0855a68ae40fdc0674ce-082ce.png 630w,\n/static/1*Scw6Tuap3aDVVldTePkFIw-d815a010304e0855a68ae40fdc0674ce-c4006.png 945w,\n/static/1*Scw6Tuap3aDVVldTePkFIw-d815a010304e0855a68ae40fdc0674ce-fa57c.png 960w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>To help solidify your knowledge about branches, have a play with this tool:</p>\n<p><a href=\"/media/aa4eedfaa49a2021a756d048590a572d/href\" title=\"/media/aa4eedfaa49a2021a756d048590a572d/href\"><strong>Learn Git Branching</strong><br>\n<em>A interactive Git visualization tool to educate and challenge!</em>pcottle.github.io</a><a href=\"/media/aa4eedfaa49a2021a756d048590a572d/href\"></a></p>\n<h3>Craft your commits</h3>\n<blockquote>\n<p>“A commit, like a well-designed function or class, should represent a single concept. A distinct, cohesive commit is easy to understand, review, and, if necessary, revert.”</p>\n</blockquote>\n<p>The real world of development is a little messy, but your source code and your git commits don’t have to represent this, and you can perfect the contents of your commit as you make it, so that your commit meets these goals — distinct, cohesive and focussed.</p>\n<p><a href=\"/media/88a3f2bb89a9828647ec8230824ce7a0/href\" title=\"/media/88a3f2bb89a9828647ec8230824ce7a0/href\"><strong>Crafting Commits in Git</strong><br>\n<em>A commit, like a well-designed function or class, should represent a single concept. A distinct, cohesive commit is…</em>blog.carbonfive.com</a><a href=\"/media/88a3f2bb89a9828647ec8230824ce7a0/href\"></a></p>\n<p><a href=\"/media/2d4732534c6ac26bd77330b17ae46fa8/href\" title=\"/media/2d4732534c6ac26bd77330b17ae46fa8/href\"><strong>Git - Interactive Staging</strong><br>\n<em>Interactive Staging Git comes with a couple of scripts that make some command-line tasks easier. Here, you'll look at a…</em>git-scm.com</a><a href=\"/media/2d4732534c6ac26bd77330b17ae46fa8/href\"></a></p>\n<h3>Use an IDE, like SourceTree</h3>\n<p>Does branching get you down, and crafting commit messages seem a little overly complex? Learning the basics with the ‘good old console’ has its advantages, but you don’t have to suffer doing things the <em>hard way</em> all the time. Using a more advanced desktop application gives you a much easier way to visualise your code changes and craft your commits, without all the complexity of its command-line counterpart.</p>\n<p><a href=\"/media/29ff2f4ddc8ea24a9a337da2166b951d/href\" title=\"/media/29ff2f4ddc8ea24a9a337da2166b951d/href\"><strong>Free Mercurial and Git Client for Windows and Mac | Atlassian SourceTree</strong><br>\n<em>SourceTree is a free Mercurial and Git Client for Windows and Mac that provides a graphical interface for your Hg and…</em>www.sourcetreeapp.com</a><a href=\"/media/29ff2f4ddc8ea24a9a337da2166b951d/href\"></a></p>","frontmatter":{"title":"Git Zero to Git Hero","date":"September 14, 2014","tags":["Medium"],"url":"https://medium.com/@psyked_james/git-zero-to-git-hero-900578ab90c2","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.7777777777777777,"src":"/static/1*fBeL90Mv68uUg15HtKliUA-c72b3c4a0a9f99c7c033f8e661eadef5-f2ac0.jpeg","srcSet":"/static/1*fBeL90Mv68uUg15HtKliUA-c72b3c4a0a9f99c7c033f8e661eadef5-567e3.jpeg 158w,\n/static/1*fBeL90Mv68uUg15HtKliUA-c72b3c4a0a9f99c7c033f8e661eadef5-c3206.jpeg 315w,\n/static/1*fBeL90Mv68uUg15HtKliUA-c72b3c4a0a9f99c7c033f8e661eadef5-f2ac0.jpeg 630w,\n/static/1*fBeL90Mv68uUg15HtKliUA-c72b3c4a0a9f99c7c033f8e661eadef5-0c4a5.jpeg 945w,\n/static/1*fBeL90Mv68uUg15HtKliUA-c72b3c4a0a9f99c7c033f8e661eadef5-4e530.jpeg 1200w","sizes":"(max-width: 630px) 100vw, 630px"}}},"description":"Mastering git requires a mentor. Let me be yours."}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/if-its-not-in-bitbucket-it-doesnt-exist/","tags":["Medium"],"title":"If it’s not in Bitbucket, it doesn’t exist.","date":"2014-09-14T12:24:15.574Z","description":"Reinforcing Development Best Practices with brutally inflexible rules.","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.7777777777777777,"src":"/static/1*2I4FcjP7sBgn0rgkTZIKIA-5131aad29f2a26443c5edcef0b6eaaf9-f2ac0.jpeg","srcSet":"/static/1*2I4FcjP7sBgn0rgkTZIKIA-5131aad29f2a26443c5edcef0b6eaaf9-567e3.jpeg 158w,\n/static/1*2I4FcjP7sBgn0rgkTZIKIA-5131aad29f2a26443c5edcef0b6eaaf9-c3206.jpeg 315w,\n/static/1*2I4FcjP7sBgn0rgkTZIKIA-5131aad29f2a26443c5edcef0b6eaaf9-f2ac0.jpeg 630w,\n/static/1*2I4FcjP7sBgn0rgkTZIKIA-5131aad29f2a26443c5edcef0b6eaaf9-0c4a5.jpeg 945w,\n/static/1*2I4FcjP7sBgn0rgkTZIKIA-5131aad29f2a26443c5edcef0b6eaaf9-4e530.jpeg 1200w","sizes":"(max-width: 630px) 100vw, 630px"}}}}},"next":{"frontmatter":{"path":"/blog/the-6-deadly-sins-of-unit-testing/","tags":null,"title":"The 6 Deadly Sins of Unit Testing","date":"2014-09-28T21:27:41.662Z","description":"Much like the ‘original 7', recognising and understanding ‘the enemy within’ helps us avoid falling into the same old traps.","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.6,"src":"/static/1*EdvNML9-fMAhW7yrrntxWA-6e9346cda3aa0abbec1f6aa2d7eca5c7-f2ac0.jpeg","srcSet":"/static/1*EdvNML9-fMAhW7yrrntxWA-6e9346cda3aa0abbec1f6aa2d7eca5c7-567e3.jpeg 158w,\n/static/1*EdvNML9-fMAhW7yrrntxWA-6e9346cda3aa0abbec1f6aa2d7eca5c7-c3206.jpeg 315w,\n/static/1*EdvNML9-fMAhW7yrrntxWA-6e9346cda3aa0abbec1f6aa2d7eca5c7-f2ac0.jpeg 630w,\n/static/1*EdvNML9-fMAhW7yrrntxWA-6e9346cda3aa0abbec1f6aa2d7eca5c7-0c4a5.jpeg 945w,\n/static/1*EdvNML9-fMAhW7yrrntxWA-6e9346cda3aa0abbec1f6aa2d7eca5c7-4e530.jpeg 1200w","sizes":"(max-width: 630px) 100vw, 630px"}}}}}}}

